.Card{
    border: 3px solid red;
    border-radius: 10px;
    overflow: hidden;
    margin: 10px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-width: 500px;
    max-width: 500px;
}

.Card .Title{
    padding: 5px 0px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.Card .Content{
    background-color: #fff;
    color: #000;
    padding: 10px;
    -ms-flex-positive: 1;
        flex-grow: 1;
}


