.Dropzone {
  height: 100px;
  width: 100px;
  background-color: #fff;
  border: 2px dashed rgb(187, 186, 186);
  border-radius: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 16px;
}

.Dropzone .Highlight {
  background-color: rgb(188, 185, 236);
}

.Dropzone .Icon {
  opacity: 0.3;
  height: 32px;
  width: 32px;
}

.Dropzone .FileInput {
  display: none;
}



